.Banner {
   position: relative;
}

.MovieInfo {
   position: absolute;
   color: white;
   top: 50%;
   left: 50%;
   transform: translateX(-50%);
}

.ImgBg {
   background: url("../../../assets/images/banner-img.jpg");
   background-position: 50%;
   background-size: cover;
   background-repeat: no-repeat;
   height: 400px;
}

.Countdown {
   color: white;
   background: #032055;
   border-top: 1px solid #17305f;
   border-bottom: 1px solid #17305f;
}

.Countdown div {
   padding-top: 10px;
   padding-bottom: 10px;
}

.Select {
   margin-top: 50px;
}

.Time {
   font-size: 16px;
   height: 100%;
   display: flex;
   align-items: center;
   background: #001232;
   padding-left: 20px;
   padding-right: 20px;
   border-radius: 12px;
}
