.Showtime {
   margin-top: 30px;
}

.Filter {
   margin-top: 30px;
   padding: 30px 10px;
   background-color: #0a1e5e;
   border-radius: 4px;
}
.TimeList {
   margin-top: 30px;
   margin-bottom: 100px;
   max-height: 400px;
   overflow: auto;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.TimeList::-webkit-scrollbar {
   display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.TimeList {
   -ms-overflow-style: none; /* IE and Edge */
   scrollbar-width: none; /* Firefox */
}

.Theater {
   height: 80px;
   background-color: #01183e;
   border-bottom: 0.5px solid rgba(168, 188, 243, 0.192);
   margin-top: 15px;
}

.TheaterName {
   padding: 0 15px;
   line-height: 78px;
}

.TheaterItem:hover {
   cursor: context-menu;
   color: rgb(196, 196, 255);
}

.Time {
   padding: 0 15px;
   line-height: 78px;
   overflow-y: hidden;
}

.Time span {
   background: url("../../../../../assets/images/ticketItem.png");
   padding: 12px 31px;
   background-repeat: no-repeat;
   margin-right: 18px;
   overflow: auto;
   white-space: nowrap;
   background-position: center;
   transition: all 0.5s;
   background-size: cover;
   cursor: pointer;
}

/* custom scrollbar */
/* width */
.Time::-webkit-scrollbar {
   height: 3px;
}

/* Track */
/* .Time::-webkit-scrollbar-track {
   background: #1d335d;
} */

/* Handle */
.Time::-webkit-scrollbar-thumb {
   background: #888;
}

/* Handle on hover */
.Time::-webkit-scrollbar-thumb:hover {
   background: #555;
}

.ItemCenter {
   display: flex;
   justify-content: center;
}

.ModalContent {
   display: flex;
   justify-content: center;
   font-size: 25px;
   color: rgb(243, 0, 203);
   font-weight: 700;
}

.ModalBtn {
   display: flex;
   justify-content: center;
   padding-top: 20px;
}
